import zod from "zod";

export function getConnectorOptionsSchema(defineOptions: unknown) {
  if (typeof defineOptions !== "function") {
    throw new Error("defineOptions must be a function");
  }
  const schema: unknown = defineOptions({ zod });
  if (!(schema instanceof zod.ZodObject)) {
    throw new Error("defineOptions must return a ZodObject schema");
  }
  return schema;
}
